*, :before, :after {
  box-sizing: border-box;
}

body {
  margin: 0;
}

a {
  color: currentColor;
  text-decoration: none;
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style: none;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

button {
  cursor: pointer;
  border: none;
  font-family: inherit;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  display: none;
  position: absolute;
  overflow: hidden;
}

.container {
  margin: 15px auto;
  padding: 0 20px;
}

.cat-info {
  margin: 15px auto;
}

.ss-main {
  width: 20%;
  max-width: 230px;
  min-width: 170px;
}

.cat-info-container {
  max-width: 375px;
  box-shadow: 2px 4px 4px hsla(0, 0, 0, .25);
  background-color: #fff;
  border-radius: 15px;
  padding: 10px;
}

.cat-info-thumb {
  height: 300px;
  width: 300px;
}

.cat-info-img {
  object-fit: contain;
  object-position: center;
}

.cat-info-title {
  text-align: center;
  margin-bottom: 10px;
}

.cat-info-desc {
